import { connect } from 'react-redux';
import { editBuildingBasic } from '../../../actions/actionCreator';
import React, { Component } from 'react'
import { Icon, Table, Button, Checkbox, Row, Col, Form } from 'antd'
import moment from 'moment';

class BasicInfo extends Component {
    state = {

    }
    componentWillMount() {

    }

    handleEdit = (e) => {
        this.props.dispatch(editBuildingBasic());
    }
   
    render() {
        const basicInfo = this.props.buildInfo.buildingBasic;
        if (basicInfo.landExpireDate && basicInfo.landExpireDate !== "") {
            basicInfo.landExpireDate = moment(basicInfo.landExpireDate).format("YYYY-MM-DD");
        }
        if (basicInfo.openDate && basicInfo.openDate !== "") {
            basicInfo.openDate = moment(basicInfo.openDate).format("YYYY-MM-DD");
        }
        if (basicInfo.deliveryDate && basicInfo.deliveryDate !== "") {
            basicInfo.deliveryDate = moment(basicInfo.deliveryDate).format("YYYY-MM-DD");
        }
        // console.log(this.props.buildInfo,basicInfo,'456')
        return (
            <div style={{ marginTop: '25px', backgroundColor: "#ECECEC" }}>
                <Form layout="horizontal">
                    <Row type="flex" style={{ padding: '1rem 0' }}>
                        <Col span={20}>
                            <Icon type="tags-o" className='content-icon' /><span className='content-title'></span>
                        </Col>
                        <Col span={4}>
                            {
                                [1, 8].includes(this.props.buildInfo.examineStatus) ? null : <Button type="primary" shape="circle" icon="edit" onClick={this.handleEdit} />
                            }
                        </Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.name} | {basicInfo.areaFullName}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.builtupArea} | {basicInfo.houseHolds}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.openDate || ''}</Col>
                        <Col span={12}>{basicInfo.deliveryDate || ''}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.propertyTerm}</Col>
                        <Col span={12}>{basicInfo.landExpireDate}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.floorSurface}</Col>
                        <Col span={12}>{basicInfo.builtupArea}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.plotRatio ? basicInfo.plotRatio : ''}%</Col>
                        <Col span={12}>{basicInfo.greeningRate ? basicInfo.greeningRate : ''}%</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.basementParkingSpace}</Col>
                        <Col span={12}>{basicInfo.parkingSpace}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.buildingNum}</Col>
                        <Col span={12}>{basicInfo.shops}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.pmc}</Col>
                        <Col span={12}>{basicInfo.pmf}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={12}>{basicInfo.developer}</Col>
                    </Row>
                    <Row className='viewRow'>
                        <Col span={24}>:{basicInfo.address}</Col>
                    </Row>
                </Form>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        buildInfo: state.building.buildInfo,
        basicData: state.basicData,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(BasicInfo);


